import React, { Component } from "react";
import { Link } from "react-router-dom";
import $ from "jquery";
import { isMobile } from "react-device-detect";
import "./layout.css";

export default class Navbar extends Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    window.addEventListener("resize", () => {
      this.forceUpdate();
    });
  }

  dropdownNavbar(button) {
    if (button === "home-button") {
      if ($("#nav-dropdown").hasClass("show")) {
        $("#nav-dropdown").toggleClass("show");
      }
    } else {
      $("#nav-dropdown").toggleClass("show");
    }
  }

  /**
   * Renders the navbar component.
   */
  render() {
    return (
      <nav
        id="main-navbar"
        className="navbar navbar-expand-sm fixed-top bg-navbar navbar-dark"
      >
        <div
          className="navbar-left"
          onClick={() => this.dropdownNavbar("home-button")}
        >
          <Link className="navbar-brand pl-1 ml-1 welcome-header-text" to="">
            NDOHERTY.DESIGN
          </Link>
        </div>
        <button
          type="button"
          className="navbar-toggler btn-white rounded"
          data-toggle="collapse"
          data-target="#nav-dropdown"
        >
          <i className="fas fa-bars p-0 m-0 text-center" />
        </button>

        <div
          className="collapse navbar-collapse navbar-right justify-content-end"
          id="nav-dropdown"
        >
          <ul className="navbar-nav mt-2 mt-lg-0">
            <li
              className="nav-item active"
              onClick={() => this.dropdownNavbar("projects-button")}
            >
              <Link className="nav-link pl-0 pr-4" to="/projects">
                PROJECTS
              </Link>
            </li>
            <li
              className="nav-item active"
              onClick={() => this.dropdownNavbar("resume-button")}
            >
              <Link className="nav-link pl-0 pr-4" to="/resume">
                RESUME
              </Link>
            </li>
            <li
              className="nav-item active"
              onClick={() => this.dropdownNavbar("contact-button")}
            >
              <Link className="nav-link pl-0 pr-4" to="/contact">
                CONTACT
              </Link>
            </li>
          </ul>
        </div>
      </nav>
    );
  }
}
