import React, { Component } from "react";
import "./css/App.css";
import { HashRouter as Router, Route, Link } from "react-router-dom";
import WelcomePage from "./components/pages/welcome/WelcomePage";
import PlanetaryDefensePage from "./components/pages/projects/planetary-defense/PlanetaryDefensePage";
import PhysicsSimPage from "./components/pages/projects/physics-sim/PhysicsSimPage";
import ActivismAppPage from "./components/pages/projects/outdoor-activism-app/ActivismAppPage";
import Navbar from "./components/layout/Navbar";
import Contact from "./components/pages/contact/Contact";
import Resume from "./components/pages/resume/Resume";
import ProjectPage from "./components/pages/projects/ProjectPage";

class App extends Component {
  render() {
    return (
      <div className="App">
        <Router>
          <header>
            <Navbar />
          </header>
          <body>
            <Route exact={true} path="/" component={WelcomePage} />
            <Route
              exact={true}
              path="/planetary-defense"
              component={PlanetaryDefensePage}
            />
            <Route
              exact={true}
              path="/electrostatic-force-simulation"
              component={PhysicsSimPage}
            />
            <Route
              exact={true}
              path="/outdoor-activism-app"
              component={ActivismAppPage}
            />
            <Route exact={true} path="/contact" component={Contact} />
            <Route exact={true} path="/projects" component={ProjectPage} />
            <Route exact={true} path="/resume" component={Resume} />
          </body>
        </Router>
      </div>
    );
  }
}

export default App;
